.proficiencies-div {
    display: grid;
    grid-template-columns: auto 260px;
    width: 446px;
    margin: 20px auto;
}

.proficiencies-title {
    font-weight: bold;
    font-size: 20px;
    color: black;

    float: left;

    margin: 4.5px 0 4.5px;
}

.proficiencies-image {
    width: 32px;
    height: 32px;

    margin: 0 10px;
}